<template>
  <div class="container">
    <div class="p1">
      <el-form :inline="true" :model="form" class="demo-form-inline">
        <el-form-item label="人名">
          <el-input v-model="form.username" placeholder="审批人"></el-input>
        </el-form-item>
        <el-form-item label="登记日期">
          <el-date-picker
            v-model="form.btime"
            type="datetime"
            placeholder="选择开始日期"
            default-time="12:00:00"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
         <el-form-item label="">
          <el-date-picker
            v-model="form.etime"
            type="datetime"
            placeholder="选择结束日期"
            default-time="12:00:00"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="query">查询</el-button>
          <el-button type="primary" @click="exportdata">导出</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="p2"></div>
    <div class="p3">
       <el-table
    :data="tableData"
    height="450"
    border
    style="width: 100%">
    <el-table-column
      prop="name"
      label="登记名称"
      width="180">
    </el-table-column>
    <el-table-column
      prop="phone"
      label="联系电话"
      width="180">
    </el-table-column>
    <el-table-column
      prop="cardnumber"
      label="身份证号">
    </el-table-column>
    <el-table-column
      prop="address"
      label="地址">
    </el-table-column>
    <el-table-column
      prop="temperature"
      label="体温">
    </el-table-column>
    <el-table-column
      prop="createtime"
      label="登记时间">
    </el-table-column>
  </el-table>
    </div>
    <div class="p4">记录总数 {{this.records}}条</div>
  </div>
</template>
<style scoped>
.container .p1{
  padding: 10px;
}
.container .p2{
  border-bottom: 1px solid #000;
}
.container .p3{
  padding: 10px;
}
.container .p4{
  padding: 10px;
}

</style>
<script>
export default {
  name: "select",
  methods:{
    query:function(){
      var param = new Object();
      if(this.form.username.length!==0){
        param.username = this.form.username;
      }
       if(this.form.btime!==null&&this.form.btime.length!==0){
        param.btime = this.form.btime;
      }
       if(this.form.etime!==null&&this.form.etime.length!==0){
        param.etime = this.form.etime;
      }
      param.currnum = 1;
      param.pagenums = 100000
       this.$axios({
        method: "get",
        url: "http://182.92.232.205:8083/info/get",
        params: param,
        headers: {} //如果需要添加请求头可在这写
      })
        .then(res => {
          console.log(res)
          var data = res.data;
          if(data.code===200){
            this.tableData = data.data;
            this.records = data.data.length;
          }
          else{
            alert("接口错误")
          }
        })
        .catch(err => {
          alert("发生错误，请联系管理员")
        });
    },
    exportdata:function(){
var param = new Object();
      if(this.form.username.length!==0){
        param.username = this.form.username;
      }
      if(this.form.btime!==null&&this.form.btime.length!==0){
        param.btime = this.form.btime;
      }
       if(this.form.etime!==null&&this.form.etime.length!==0){
        param.etime = this.form.etime;
      }
      param.currnum = 1;
      param.pagenums = 100000
       this.$axios({
        method: "get",
        url: "http://182.92.232.205:8083/info/export",
        params: param,
        responseType: 'blob',
        headers: {
          // responseType: 'blob'
        } //如果需要添加请求头可在这写
      })
        .then(res => {
          const blob = new Blob([res.data], {type: "application/vnd.ms-excel"});
         const fileName = '人员登记表.xlsx';
         const elink = document.createElement('a');
         elink.download = fileName;
         elink.style.display = 'none';
         elink.href = URL.createObjectURL(blob);
         document.body.appendChild(elink);
         elink.click();
         URL.revokeObjectURL(elink.href); // 释放URL 对象
         document.body.removeChild(elink);
          // console.log(res)
          // var data = res.data;
          // if(data.code===200){
          //   // this.tableData = data.data;
          //   // this.records = data.data.length;
          // }
          // else{
          //   alert("接口错误")
          // }
        })
        .catch(err => {
          alert("发生错误，请联系管理员")
        });
    }
  },
  data() {
    return {
      message: "select",
      form: {
        username: "",
        btime: "",
        etime: "",
      },
      records:0,
      tableData: []
    };
  }
};
</script>



