<template>
  <div class="container">
    <div class="title">
      <img src="../assets/honglinhuasheng.png" style="width:100%;height:100%" />
    </div>
    <div class="title2">{{getDate()}}登记成功，感谢您的支持！</div>
  </div>
</template>
<style scoped>
.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.container .title {
  /* background-color: blue; */
}
.container .title2 {
  color: #13ce66;
}
</style>
<script>
export default {
  name: "success",
  methods: {
    getDate: function() {
      var date = new Date();
      var year = date.getFullYear();
      var month = (date.getMonth() + 1).toString().padStart(2,"0");
      var day = date.getDate().toString().padStart(2,"0");
      var hour = date.getHours().toString().padStart(2,"0");
      var minute = date.getMinutes().toString().padStart(2,"0");
      var second = date.getSeconds().toString().padStart(2,"0");
      return `${year}-${month}-${day} ${hour}:${minute}:${second}`
    }
  },
  data() {
    return {
      message: "success"
    };
  }
};
</script>


