<template>
  <div class="container">
    <div class="p1">
      <div class="title">抗击新冠肺炎</div>
      <div class="title-1">为了疫情可控，请大家主动登记</div>
    </div>
    <div class="p2">
      <el-form :label-position="labelPosition" ref="form" :model="form">
        <el-form-item label="人员名称">
          <el-input v-model="form.username" placeholder="必填"></el-input>
        </el-form-item>
        <el-form-item label="手机号码">
          <el-input v-model="form.phone" placeholder="必填"></el-input>
        </el-form-item>
        <el-form-item label="身份证号码">
          <el-input v-model="form.cardnumber" placeholder="必填"></el-input>
        </el-form-item>
        <el-form-item label="居住地址">
          <el-input v-model="form.address" placeholder="如道路、门牌号、楼栋号、单元室等"></el-input>
        </el-form-item>
        <el-form-item label="测量体温">
          <el-input v-model="form.temperature" placeholder="请填写实际体温"></el-input>
        </el-form-item>
        <el-form-item>
          <label class="error">{{errorinfo.context}}</label>
        </el-form-item>
      </el-form>
    </div>
    <div class="p3">
      <el-button type="primary" @click="onSubmit">登记</el-button>
    </div>
  </div>
</template>
<style scoped>
/deep/.el-form-item {
  margin-bottom: 0px;
}
.container {
  width: 100%;
  height: 100%;
  /* background-color: red; */
  background-color: #009ad6;
}
.container .p1 {
  width: 90%;
  height: 10%;
  margin: 0 auto;
  padding: 10px;
  color: #fff;
  /* background-color: red; */
}
.container .p1 .title {
  font-size: larger;
}
.container .p1 .title-1 {
  font-size: small;
  margin-top: 10px;
  background-color: rosybrown;
  width: fit-content;
  /* border-radius: 10px; */
  padding: 0px 4px;
}
.container .p2 {
  padding: 10px 10px;
  /* height: 80%; */
  width: 80%;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 5px 5px 0px 0px;
}
.container .p3 {
  /* height: 100px; */
  padding: 10px 10px;
  width: 80%;
  margin: 0 auto;
  background-color: #fff;
  text-align: center;
  border-radius: 0px 0px 5px 5px;
}
.error {
  color: red;
}
</style>
<script>
export default {
  name: "infomationadd",
  methods: {
    onSubmit: function() {
      // var reg = /^\d+$/
      var reg = /^[\u4E00-\u9FA5A-Za-z0-9_]+%/;
      var reg1 = /^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})(\d{1}|(X|x))$/;
      var reg2 = /^(\-)?\d+(\.\d{1,2})?$/;
      // var reg3 = /^(\d{3,4}|\d{3,4}-)?\d{7,14}$/;
      var reg4 = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
      this.errorinfo.error = false;
      if (this.form.username.length === 0) {
        this.errorinfo.error = true;
        this.errorinfo.context = "用户名不能为空";
        console.log(1);
        return false;
      }

      if (!reg4.test(this.form.phone)) {
        this.errorinfo.error = true;
        this.errorinfo.context = "手机号码格式错误";
        console.log(22);
        return false;
      }

      if (!reg1.test(this.form.cardnumber)) {
        this.errorinfo.error = true;
        this.errorinfo.context = "身份证号码错误";
        console.log(3);
        return false;
      }
      if (this.form.temperature.length != 0) {
        if (!reg2.test(this.form.temperature)) {
          this.errorinfo.error = true;
          this.errorinfo.context = "体温输入错误";
          console.log(4);
          return false;
        }
      } else {
        this.form.temperature = "0";
      }

      // alert(this.form);
      this.$axios({
        method: "post",
        url: "http://182.92.232.205:8083/info/insert",
        data: this.form,
        //withCredentials:true, //局部携带cookie
        headers: {} //如果需要添加请求头可在这写
      })
        .then(res => {
          //res是返回结果
          console.log(res.data.code);
          if (res.data.code === 200) {
            this.$router.push({ name: "success" });
          } else {
            this.$router.push({ name: "error" });
          }
          // console.log(res.data)
        })
        .catch(err => {
          //请求失败就会捕获报错信息
          //err.response可拿到服务器返回的报错数据
        });
    }
  },
  data() {
    return {
      labelPosition: "top",
      errorinfo: {
        error: false,
        context: " "
      },
      form: {
        username: "",
        phone: "",
        cardnumber: "",
        address: "",
        temperature: ""
      }
    };
  }
};
</script>


